'use client';
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { useParams, useRouter } from 'next/navigation';

const ProgramCoursesPage = () => {
  const params = useParams();
  const router = useRouter();
  const [programId, setProgramId] = useState(null);
  const [programName, setProgramName] = useState('');
  const [courses, setCourses] = useState([]);
  const [periods, setPeriods] = useState([]);
  const [tableRows, setTableRows] = useState([]);
  const [loading, setLoading] = useState(true);
  const [submitting, setSubmitting] = useState(false);
  const [error, setError] = useState(null);
  const [successMessage, setSuccessMessage] = useState('');

  // Initialize table with 10 empty rows
  useEffect(() => {
    const initializeTable = () => {
      const emptyRows = Array(10).fill().map((_, index) => ({
        id: index,
        periodId: '',
        courseId: ''
      }));
      setTableRows(emptyRows);
    };

    initializeTable();
  }, []);

  useEffect(() => {
    const fetchData = async () => {
      setLoading(true);
      setError(null);
      
      try {
        // Get programId from URL or localStorage
        const idFromUrl = params.programId;
        const idFromStorage = localStorage.getItem('selectedProgramId');
        const resolvedProgramId = idFromUrl || idFromStorage;
        
        if (!resolvedProgramId) {
          throw new Error('No program ID found');
        }
        
        setProgramId(resolvedProgramId);
        
        // Get program details
        const programResponse = await axios.get(`http://localhost:9921/programs/${resolvedProgramId}`);
        setProgramName(programResponse.data.programName);
        
        // Get all available courses
        const coursesResponse = await axios.get('http://localhost:9921/courses');
        setCourses(coursesResponse.data);
        
        // Get all available periods
        const periodsResponse = await axios.get('http://localhost:9921/periods');
        setPeriods(periodsResponse.data);
        
        setLoading(false);
      } catch (err) {
        console.error('Error fetching data:', err);
        setError(err.message || 'Failed to load required data');
        setLoading(false);
      }
    };
    
    fetchData();
  }, [params]);

  const handleInputChange = (rowId, field, value) => {
    setTableRows(prevRows => 
      prevRows.map(row => 
        row.id === rowId ? { ...row, [field]: value } : row
      )
    );
    
    // Clear any previous success message when user makes changes
    if (successMessage) {
      setSuccessMessage('');
    }
  };

  const validateForm = () => {
    // Filter out empty rows (where both period and course are empty)
    const filledRows = tableRows.filter(row => row.periodId || row.courseId);
    
    // Check if any row has only one field filled
    const invalidRows = filledRows.filter(row => 
      (row.periodId && !row.courseId) || (!row.periodId && row.courseId)
    );
    
    if (invalidRows.length > 0) {
      setError('Some rows have missing period or course. Please complete or clear these rows.');
      return false;
    }
    
    // At least one row must be filled
    if (filledRows.length === 0) {
      setError('Please fill at least one row before submitting.');
      return false;
    }
    
    return true;
  };

  const handleSubmit = async () => {
    setError(null);
    
    if (!validateForm()) {
      return;
    }
    
    setSubmitting(true);
    
    try {
      // Filter out empty rows
      const filledRows = tableRows.filter(row => row.periodId && row.courseId);
      
      // Prepare data for junction table
      const junctionTableData = filledRows.map(row => ({
        programId: programId,
        periodId: row.periodId,
        courseId: row.courseId
      }));
      
      // Submit the data to your backend
      await axios.post('http://localhost:9921/programcourses/mapping', junctionTableData);
      
      // Reset the table after successful submission
      const emptyRows = Array(10).fill().map((_, index) => ({
        id: index,
        periodId: '',
        courseId: ''
      }));
      
      setTableRows(emptyRows);
      setSuccessMessage('Course mappings saved successfully!');
      
      // Auto-dismiss success message after 5 seconds
      setTimeout(() => {
        setSuccessMessage('');
      }, 5000);
      
    } catch (err) {
      console.error('Error submitting data:', err);
      setError(err.response?.data?.message || 'Failed to save course mappings. Please try again.');
    } finally {
      setSubmitting(false);
    }
  };

  const handleClearTable = () => {
    const emptyRows = Array(10).fill().map((_, index) => ({
      id: index,
      periodId: '',
      courseId: ''
    }));
    setTableRows(emptyRows);
    setError(null);
    setSuccessMessage('');
  };

  if (loading) {
    return (
      <div className="p-6">
        <div className="flex items-center justify-center h-40">
          <div className="text-gray-600">Loading program course mapping...</div>
        </div>
      </div>
    );
  }

  return (
    <div className="p-6">
      <div className="mb-6">
        <h3 className="text-lg text-green-900 font-semibold">
          Program Course Mapping: {programName || programId}
        </h3>
        <p className="text-sm text-gray-500">
          Assign courses to periods for this program
        </p>
      </div>

      {error && (
        <div className="mb-4 p-3 bg-red-100 border border-red-400 text-red-700 rounded">
          <strong>Error:</strong> {error}
        </div>
      )}

      {successMessage && (
        <div className="mb-4 p-3 bg-green-100 border border-green-400 text-green-700 rounded">
          {successMessage}
        </div>
      )}

      <div className="bg-white rounded-lg shadow-md overflow-hidden mb-6">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Row
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Period
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Course Name
              </th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {tableRows.map((row, index) => (
              <tr key={row.id}>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                  {index + 1}
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <select
                    value={row.periodId}
                    onChange={(e) => handleInputChange(row.id, 'periodId', e.target.value)}
                    className="block w-full py-2 px-3 border border-gray-300 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                  >
                    <option value="">Select Period</option>
                    {periods.map((period) => (
                      <option key={period.periodId} value={period.periodId}>
                        {period.periodName}
                      </option>
                    ))}
                  </select>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <select
                    value={row.courseId}
                    onChange={(e) => handleInputChange(row.id, 'courseId', e.target.value)}
                    className="block w-full py-2 px-3 border border-gray-300 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
                  >
                    <option value="">Select Course</option>
                    {courses.map((course) => (
                      <option key={course.courseId} value={course.courseId}>
                        {course.courseName}
                      </option>
                    ))}
                  </select>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>

      <div className="flex justify-end space-x-4">
        <button
          onClick={handleClearTable}
          className="px-4 py-2 bg-gray-500 text-white rounded-md text-sm font-medium hover:bg-gray-600"
        >
          Clear Table
        </button>
        <button
          onClick={handleSubmit}
          disabled={submitting}
          className="px-4 py-2 bg-indigo-600 text-white rounded-md text-sm font-medium hover:bg-indigo-700 disabled:bg-indigo-300"
        >
          {submitting ? 'Saving...' : 'Save Mappings'}
        </button>
      </div>
    </div>
  );
};

export default ProgramCoursesPage;